import { Component, OnInit } from '@angular/core';
import { FetchdataserviceService } from '../fetchdataservice.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Product } from '../addproduct/product';

@Component({
  selector: 'app-editprod',
  templateUrl: './editprod.component.html',
  styleUrls: ['./editprod.component.css']
})
export class EditprodComponent implements OnInit {
pro=new Product();
  constructor(private rt:Router,private ser:FetchdataserviceService,private ar:ActivatedRoute) { }
  updateprodformsubmit(){
   
    this.ser.addusertoremote(this.pro).subscribe(
      data=>{
        console.log("succesfully added");
        this.rt.navigate(['listproducts']);
      },
      error=>console.log("error occured")
      
    )

  }
  gotolist(){
    this.rt.navigate(['listproducts']);
  }

  ngOnInit(): void {
    let id=parseInt(this.ar.snapshot.paramMap.get('id'));
    this.ser.fetchprobyid(id).subscribe(
      data=>{
this.pro=data;
      }
    )
  }

}
