import { Component, OnInit,Input } from '@angular/core';
import { Product } from '../addproduct/product';
import { FetchdataserviceService } from '../fetchdataservice.service';

@Component({
  selector: 'app-cartpage',
  templateUrl: './cartpage.component.html',
  styleUrls: ['./cartpage.component.css']
})
export class CartpageComponent implements OnInit {
req:number;
total:any=0;
arr:Array<Product>=[];
bol:String;
p=new Product();

  constructor(private ser:FetchdataserviceService) {
    
   }

  ngOnInit(): void {
    
  
  }
  searchpro(){
    this.req  = parseFloat((<HTMLInputElement>document.getElementById("x")).value);
this.ser.fetchprobyid(this.req).subscribe(
  data=>{
this.bol="true";
this.p=data;
this.arr.push(this.p);

  },
  error=>{
this.bol="false";
  }
)
  }
  
  generatebill(){
    
    for(let dat of this.arr) {
      let sum=dat.prod_cost;
      this.total=this.total+sum;
    }
  }



}
