import { Component, OnInit } from '@angular/core';
import { FetchdataserviceService } from '../fetchdataservice.service';
import { ActivatedRoute } from '@angular/router';
import { Product } from '../addproduct/product';

@Component({
  selector: 'app-viewproducts',
  templateUrl: './viewproducts.component.html',
  styleUrls: ['./viewproducts.component.css']
})
export class ViewproductsComponent implements OnInit {
pro=new Product();
  constructor(private ser:FetchdataserviceService,private ar:ActivatedRoute) { }

  ngOnInit(): void {
    let id=parseInt(this.ar.snapshot.paramMap.get('id'));
    this.ser.fetchprobyid(id).subscribe(
      data=>{
this.pro=data;
      }
    )
  }

}
