import { Component, OnInit } from '@angular/core';
import { FetchdataserviceService } from '../fetchdataservice.service';
//import { Route } from '@angular/compiler/src/core';
import { Router } from '@angular/router';
import { Observable, from } from 'rxjs';
import { Product } from '../addproduct/product';
import { DataserService } from '../dataser.service';


@Component({
  selector: 'app-prodlist',
  templateUrl: './prodlist.component.html',
  styleUrls: ['./prodlist.component.css'],
  
})
export class ProdlistComponent implements OnInit {
pro: Observable<Product[]>;
arr: Array<Product>=[];
count:number=0;

  constructor(private ser:FetchdataserviceService,private rt:Router,private data:DataserService) { }

  ngOnInit() {
    
    this.pro=this.ser.fetchprolist();
  }
  gotoaddpro(){
    this.rt.navigate(['addproduct']);
  }
  gotoeditpro(d:number){
    this.rt.navigate(['editproduct',d]);
  }
  gotoviewpro(d1:number){
    this.rt.navigate(['viewproduct',d1]);
  }
  gotodelpro(d2:number){
    this.rt.navigate(['delpro',d2]);
  }
  addtocart(i:Product){
    this.arr.push(i);
  }
  gotocartpage(){
    this.rt.navigate(['cartpage']);
  }

}
