import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProdlistComponent } from './prodlist/prodlist.component';
import { AddproductComponent } from './addproduct/addproduct.component';
import { EditprodComponent } from './editprod/editprod.component';
import { ViewproductsComponent } from './viewproducts/viewproducts.component';
import { CartpageComponent } from './cartpage/cartpage.component';
import { DelproComponent } from './delpro/delpro.component';


const routes: Routes = [
  {path:'',component:ProdlistComponent},
  {path:'addproduct',component:AddproductComponent},
  {path:'editproduct',component:EditprodComponent},
  {path:'editproduct/:id',component:EditprodComponent},
  {path:'viewproduct/:id',component:ViewproductsComponent},
  {path:'listproducts',component:ProdlistComponent},
  {path:'cartpage',component:CartpageComponent},
  {path:'delpro/:id',component:DelproComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
