import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FetchdataserviceService } from '../fetchdataservice.service';
import { Product } from './product';

@Component({
  selector: 'app-addproduct',
  templateUrl: './addproduct.component.html',
  styleUrls: ['./addproduct.component.css']
})
export class AddproductComponent implements OnInit {
  product :Product;
  

  constructor(private rt:Router,private ser:FetchdataserviceService) { 
    
  }

  ngOnInit() {
    this.product=new Product();
  }
  addprodformsubmit(){
this.ser.addusertoremote(this.product).subscribe(
  data=>{
    console.log("succesfully added");
    this.rt.navigate(['listproducts']);
  },
  error=>console.log("error occured")
  
)
  }
  gotolist(){
    this.rt.navigate(['listproducts']);
  }

}
