import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import { Observable } from 'rxjs';
import { Product } from './addproduct/product';
@Injectable({
  providedIn: 'root'
})
export class FetchdataserviceService {

  constructor(private http:HttpClient) { }
fetchprolist():Observable<any>{
  return this.http.get<any>("https://8080-fffadaebdfcdbfabbaeecbfabdebcd.examlyiopb.examly.io/productslist");
}
addusertoremote(product:Product):Observable<any>{
return this.http.post<any>("https://8080-fffadaebdfcdbfabbaeecbfabdebcd.examlyiopb.examly.io/addproduct",product);
}
fetchprobyid(id:number):Observable<any>{
return this.http.get<any>("https://8080-fffadaebdfcdbfabbaeecbfabdebcd.examlyiopb.examly.io/getpbid/"+id);

}
delprobyid(id:number):Observable<any>{
  return this.http.get<any>("https://8080-fffadaebdfcdbfabbaeecbfabdebcd.examlyiopb.examly.io/delpdid"+id);
}

}
